.navbar {
    width: 100%;
    height: 45px;
    background-color: rgb(0 102 167);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    z-index: 100000000;
}
.nav-logo {
    margin-top: -2px;
    margin-left: 40px;
}
.nav-logo:hover {
    cursor: pointer;
}
.account {
    display: flex;
    align-items: center;
    margin-right: 40px;
    padding-left: 25px;
    border-left: 1px solid white;
    cursor: pointer;
}
.account img {
    height: 28px;
    margin-right: 10px;
}
.account-content {
    color: white;
}
.my-account {
    font-weight: bolder;
    font-size: 12px;
    margin-bottom: 2px;
}
.login {
    font-size: 10px;
    color: rgb(84 202 246);
}